import Vue from 'vue'
import Router from 'vue-router'
import home from './components/home.vue'//首页
import ke from './components/ke.vue'//课程
import about from './components/about.vue'//关于
import loginup from './components/loginup.vue'//登录/注册
import go from './components/go.vue'//登录/注册
//底部
import ys from './components/ys.vue'//隐私
import bq from './components/bq.vue'//版权
import th from './components/th.vue'//退换
//
import loginin from './components/loginin.vue'
import shops from './components/shops.vue'
Vue.use(Router)

export default new Router({
	routes:[
		{
			path:'/',
			name:'home',
			component:home,
		},
		{
			path:'/ke',
			name:'ke',
			component:ke
		},
		{
			path:'/about',
			name:'about',
			component:about
		},
		{
			path:'/loginup',
			name:'loginup',
			component:loginup
		},
		{
			path:'/ys',
			name:'ys',
			component:ys
		},
		{
			path:'/bq',
			name:'bq',
			component:bq
		},
		{
			path:'/th',
			name:'th',
			component:th
		},{
			path:'/loginin',
			name:'loginin',
			component:loginin
		}
		,{
			path:'/go',
			component:go
		},{
			path:'/:id',
			name:'shops',
			component:shops
		}
	]
})



