<template>
	<div>
		<div id="navs">
			<div class="row navs_box">
				<div class="col-md-12 col-sm-12 col-xs-12">
					<ul>
						<li v-for="(i,index) in arr">
							<router-link :to="router[index]">
								{{i}}
							</router-link>
						</li>
						
					</ul>
				</div>
			</div>
		</div>
		<div class="min_nav">
			<div class="show" @click="btn" style="padding-left: 25px;">{{show}}<span class="glyphicon glyphicon-chevron-down" style="float: right; line-height: 44px; padding-right: 15px;"></span></div>
			<ul class="min_nav_list" v-show="type">
				<li v-for="(i,$index) in arr" @click="navs($index)">
					<router-link :to='router[$index]'>
						{{i}} 
					</router-link>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				arr: ['首页', '课程分类', '关于我们', '登录/注册','go'],
				router: ['/', 'ke', 'about', 'loginup','go'],
				num: 0,
				show: '首页',
				click: '',
				type: false
			}
		},

		methods: {
			btn() {
				this.type = !this.type;
				if(this.show != '关闭') {
					this.click = this.show;
					this.show = '关闭';
				} else {
					this.show = this.click;
				}
			},
			navs(i) {
				this.num = i;
				this.type = !this.type;
				this.show = this.arr[i];
			}
		}

	}
</script>

<style>
	* {
		margin: 0;
		padding: 0;
	}
	
	#navs ul {
		padding-left: 12%;
		list-style: none;
		overflow: hidden;
		margin-bottom: 0;
		background: #363942;
		height: 50px;
		line-height: 50px;
	}
	
	#navs ul li {
		height: 50px;
		width: 10%;
		line-height: 50px;
		text-align: center;
		margin-bottom: 0px;
		float: left;
	}
	
	#navs ul li a {
		text-decoration: none;
		color: #fff;
	}
	@media only screen and (min-width:0px)and (max-width:992px) {
		#navs{
			display: none;
		}
		.min_nav{
			display: block !important;
		}
	}
	.min_nav {
		display: none;
		width: 100%;
		height: 44px;
		line-height: 44px;
		text-align: center;
		color: #fff;
		font-size: 14px;
	}
	
	.show {
		background: #363942;
	}
	
	.min_nav_list {
		width: 100%;
		position: relative;
		z-index: 999;
	}
	
	.min_nav_list li {
		width: 100%;
		background: #363942;
		border-bottom: 1px #f4f7f7 solid;
	}
	
	.min_nav_list li a {
		width: 100%;
		height: 100%;
		color: #fff;
		display: block;
		text-decoration: none;
	}
</style>


