<template>
	<div>
		<div class="container">
			<div class="row" style="width: 100%; margin-bottom: 40px;">
				<div class="bitcommerce-message bitcommerce-success" style="margin-left: 20px;width: 98%;color: #2f9a4c;" v-if='g_gwcsj.length'>
					<i class="glyphicon glyphicon-ok" style="color: #2f9a4c;margin: 0 10px;font-size: 14px;"></i>
					<router-link to='/ke'>
						<a class="ddd button wc-forward" style="text-decoration: none;position: absolute;top:16px;">继续购物</a>
						成功添加进您的购物车.
					</router-link>
				</div>
				<div class="bitcommerce-message bitcommerce-success" style="margin-left: 20px;width: 98%;" v-if='!g_gwcsj.length'>
					<i class="glyphicon glyphicon-ok" style="color: #2f9a4c;margin: 0 10px;font-size: 14px;"></i> 购物车已更新。
				</div>
			</div>
			<div v-if='!g_gwcsj.length' class='qfy_wc_div'>
				<p class="cart-empty" style="margin-top:50px;text-align:center;">您的购物车目前是空的。</p>
				<p style="margin:0 auto;text-align:center;" class="return-to-shop">
					<router-link to='/ke'>
						<a class="button wc-backward second_button">
							<span class="glyphicon glyphicon-arrow-left" style="padding-right: 10px;color: #777777;"></span>返回商店</a>
					</router-link>
				</p>
			</div>
			<div class="row go_box" v-if="g_gwcsj.length">
				<div class="col-md-8 col-xs-12 col-sm-12 go_left">
					<table class="table">
						<tr class="tr">
							<th class="frist" colspan="3">商品</th>
							<th>价格</th>
							<th>数量</th>
							<th>合计</th>
						</tr>
						<tr v-for="(i,$index) in g_gwcsj" class="secones">
							<td class="product-removelink">
								<a @click="delete_actions(i.title)" class="remove" style="border:0;cursor:pointer;" title="移除本项" data-key="d5776aeecb3c45ab15adce6f5cb355f3">
									<i style="font-size:18px;" class="glyphicon glyphicon-remove-circle"></i>
								</a>
							</td>
							<td class="simg" style="padding-top: 20px;">
								<img :src="i.img" alt="" class="boximg" />
							</td>
							<td style="padding-left: 20px;font-weight: 600;">
								<span>{{i.title}}</span>
							</td>
							<td>
								<span class="spans">{{i.money}}</span>
							</td>
							<td style="padding-right: 10px;">
								<span class="spans">{{i.s}}</span>
							</td>
							<td>
								<span class="spans">{{i.money}}</span>
							</td>
						</tr>
					</table>
				</div>
				<div class="col-md-3 col-xs-12 col-sm-12 go_right">
					<h2 class="gwczj">购物车总计</h2>
					<div class="gw_div">
						<table class="gw_table" cellspacing="0" style="width: 100%;">
							<tbody>
								<tr class="cart-subtotal">
									<th>购物车小计</th>
									<td>
										<p class="amount">￥{{zj}}.00</p>
									</td>
								</tr>
								<tr class="shipping ">
									<th>运费</th>
									<td>
										免费送货 <input type="hidden" name="shipping_method[0]" data-index="0" id="shipping_method_0" value="free_shipping" class="shipping_method">
									</td>
								</tr>
								<tr class="order-total">
									<th>订单总计</th>
									<td><span class="amount">￥{{zj}}.00</span></td>
								</tr>
							</tbody>
						</table>
						<button class="cashier">前往收银台</button>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { mapGetters, mapActions } from 'vuex'
	export default {
		computed: {
			...mapGetters(['g_gwcsj']),
			zj() {
				var sum = 0
				for(var i = 0; i < this.g_gwcsj.length; i++) {
					sum += this.g_gwcsj[i].s * this.g_gwcsj[i].money
				}
				return sum
			}
		},
		methods: {
			...mapActions(['delete_actions'])
		},
	}
</script>

