			color: #bbb;
		}
		.simg {
			width: 20%;
			height: auto;
		}
		.boximg {
			width: 100%;
			height: auto;
		}
		.product-removelink {
			width: 50px;
		}
		.gwczj {
			font-size: 16px;
			font-weight: bold;
			color: rgb(85, 85, 85);
			font-family: 微软雅黑;
			padding-top: 6px;
			padding-bottom: 6px;
			border-bottom: 3px #ccc solid;
		}
		.gw_div {
			width: 100%;
			height: auto;
		}
		.cart-subtotal,
		.shipping {
			color: #666666;
		}
		.cart-subtotal th,
		.shipping th {
			font-size: 14px;
			font-weight: bold;
			color: #777777;
			padding-bottom: 12px;
			padding-top: 4px;
			text-align: left;
		}
		.cart-subtotal td span,
		.shipping td span {
			font-size: 14px;
			font-weight: normal;
			color: #777777;
			line-height: 36px;
			text-align: right;
		}
		.order-total {
			border-bottom: 3px solid #ddd;
			border-top: 1px solid #ddd;
			border-color: #dddddd;
			color: #666;
			line-height: 40px;
		}
		.cashier {
			width: 200px;
			height: 40px;
			font-size: 16px;
			font-weight: normal;
			color: #ffffff;
			border-color: #777777;
			background-color: #777777;
			text-align: center;
			line-height: 40px;
			outline: none;
			border: none;
			margin-top: 20px;
			margin-left: 20px;
		}
		.cashier:hover {
			background: #000;
		}
	}
