import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
var state = {
	state_arr: [],
	state_arrs: [],
	gwcsj:[]

}
var getters = {
	getters_arr(state) {
		return state.state_arr
	},
	getters_arrs(state) {
		return state.state_arrs
	},
	
	getters_arr_zj(state) {
		var num = 0;
		for (var i = 0; i < state.state_arrs.length; i++) {
			num += state.state_arrs[i].jg * state.state_arrs[i].s
		}
		return num;
	},
	g_gwcsj(state){
		return state.gwcsj
	}

}

var mutations = {
	mutations_btn(state, date) {
		state.state_arr.splice(0, 1, date)
	},
	
	mutationss_btn(state, date) {
		var nametype = true
		for(var i=0;i<state.state_arrs.length;i++){
			if(state.state_arrs[i].name == date.name){
				state.state_arrs[i].s++
				nametype = false
			}
		}
		if(nametype){
			state.state_arrs.push(date)
		}	
	},


	jia_mutations(state, date) {
		for (var i = 0; i < state.state_arrs.length; i++) {
			if (state.state_arrs[i].name == date.name) {
				state.state_arrs[i].s++
				console.log(date)
				state.state_arrs.splice(i, 1, state.state_arrs[i])
			}
		}
	},
	jian_mutations(state, date) {
		for (var i = 0; i < state.state_arrs.length; i++){
			if (state.state_arrs[i].name == date.name) {
				if (state.state_arrs[i].s == 1) {
					state.state_arrs[i].s = 1
				} else {
					state.state_arrs[i].s--
				}
				state.state_arrs.splice(i, 1, state.state_arrs[i])

			}
		}
	},
	deletes(state, index) {
		var n=0;
		for (var i = 0; i < state.gwcsj.length; i++) {
			if( state.gwcsj[i].title == index){
				state.gwcsj.splice(i,1)
			}
			
		}
		
	},
	m_gwcsj(state,index){
		var kg=true;
		for (var i=0;i<state.gwcsj.length;i++) {
			if (index.title==state.gwcsj[i].title) {
				kg=false;
				alert('购物车已存在')
			}
		}
		if (kg) {
			state.gwcsj.push(index);
		}
		
	}

}
var actions = {
	actions_btn({
		commit
	}, data) {
		commit('mutations_btn', data)
	},
	jia_actions({
		commit
	}, data) {
		commit('jia_mutations', data)
	},
	jian_actions({
		commit
	}, data) {
		commit('jian_mutations', data)
	},
	delete_actions({
		commit
	}, data) {
		commit('deletes', data)
	},
	data_actions({
		commit
	}, data) {
		commit('mutationss_btn', data)
		console.log(data)
	},
	a_gwcsj({
		commit
	}, data) {
		commit('m_gwcsj', data)
		console.log(data)
	},
}

export default new Vuex.Store({
	state,
	getters,
	mutations,
	actions
})



